body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

html, body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.cuadrado {
  display: -webkit-flex;
  display: flex;
  margin: 0 auto;
  z-index: 1;
  position: absolute;
  bottom: 12.5%;
  left: 48.3%;
  width: 1.2vw;
}

.bell {
  display: -webkit-flex;
  display: flex;
  margin: 0 auto;
  z-index: 2;
  position: absolute;
  left: 44.3%;
  bottom: 63%;
  width: 9vw;
}

.fairAnimation{
  -webkit-animation-name: infinite-spinning;
          animation-name: infinite-spinning;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  }

@-webkit-keyframes infinite-spinning {
  0% { bottom: 12.5%; }
  60% { bottom: 40vw; }
  60.1% {bottom: 39vw}
  100% {bottom: 12.5%}
}

@keyframes infinite-spinning {
  0% { bottom: 12.5%; }
  60% { bottom: 40vw; }
  60.1% {bottom: 39vw}
  100% {bottom: 12.5%}
}

.bellAnimation {
  -webkit-animation-name: bell-resizing;
          animation-name: bell-resizing;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  }

@-webkit-keyframes bell-resizing {
  0% {height: 9vw; width: 9vw; left: 44.3%}
  90% {height: 9vw; width: 9vw; left: 44.3%}
  93% {height: 13vw; width: 13vw; left: 43%}
  94% {height: 9vw; width: 9vw; left: 44.3%}
  97% {height: 13vw; width: 13vw; left: 43%}
  100% {height: 9vw; width: 9vw; left: 44.3%}
}

@keyframes bell-resizing {
  0% {height: 9vw; width: 9vw; left: 44.3%}
  90% {height: 9vw; width: 9vw; left: 44.3%}
  93% {height: 13vw; width: 13vw; left: 43%}
  94% {height: 9vw; width: 9vw; left: 44.3%}
  97% {height: 13vw; width: 13vw; left: 43%}
  100% {height: 9vw; width: 9vw; left: 44.3%}
}

.fair{
  z-index: -1;
  width: 50vw;
  
}
